@import url(https://fonts.googleapis.com/css?family=Exo+2);
body {
  margin: 0;
  padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
html {
  font-family: "Exo 2";
}

.header-nav {
  background: #43c6ac; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #f8ffae, #43c6ac);
  color: white;
  opacity: 0.95; /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landingpage {
  background: #43c6ac; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f8ffae,
    #43c6ac
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img {
  height: 150px;
  width: 150px;

  margin-top: 6em;
  padding: 2em;
  border-radius: 50%;
}

.text-box {
  text-align: center;
}
.banner-text {
  display: inline-block;
  background-color: black;
  opacity: 0.65;
  width: 50%;
  border-radius: 10px;
}

.banner-text h1 {
  font-family: "Exo 2";
  padding: 0px;
  margin: 10px 10px 0px 10px;
  font-size: 32px;
  color: white;
}

.banner-text p {
  color: white;
  margin: 10px;
}

.social {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
  padding-bottom: 1em;
}

.social i {
  color: white;
  font-size: 3em;
}

